.title {
    font-size: 24px;
    font-weight: 300;
    margin-bottom: 30px;
}


/* OUTPUT LIST */
.list {
    margin-top: 10px;
}

.outputs_list, .inputs_list {
    width: 270px;
    margin-right: 10px;
    height: 100%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: inline-block;
    vertical-align: top;
    white-space: nowrap;

    & h1 {
        font-size: 16px;
        text-align: left;
        font-weight: 500;
        /* padding-left: 10px;
        padding-top: 10px; */
        margin: 0;
        margin-top: 5px;
        text-transform: capitalize;
        letter-spacing: .5px;
    }
}

.outputs_card, .inputs_card {
    background: #333C44;
    border-radius: 3px;
    overflow: hidden;
    padding: 6px 6px 2px 8px;
    position: relative;
    z-index: 10;
    display: -ms-flexbox;
    cursor: pointer;
    margin-bottom: 6px;
    max-width: 300px;
    min-height: 20px;
    position: relative;
    z-index: 0;
    padding: 10px;
    & span {
      color: white;
      font-size: 14px;
      font-weight: 500;
    }
}

.inputs_card {
    background: transparent;
    border: 1px solid #333C44;
    & span {
        color: black;
    }
}


