@import 'react-datepicker/dist/react-datepicker.css';

body {
    margin: 0;
    padding: 20px;
    font-family: 'Fira Sans';
    /* lost-utility: edit;  */
  }
    
  .canvas_title {
    margin-top: 0;
    margin-left: 5px;
    margin-bottom: 20px;
    font-size: 14px;
    letter-spacing: 1px;
    color: #333;
    padding-bottom: 10px;
    border-bottom: 1px solid #dadada;
  }


  .wrapper {
    height: auto;
  }

  .lists {
    /* background: #f0d2d1; */
    height: auto;
    /* lost-align: center; */
    lost-column: 1/2;
    lost-offset: 1/4;
    padding: 20px;
    & h2 {
      text-align: left;
      font-size: 36px;
      font-weight: 700;
      color: #100F2B;
    }
    & .wrapper {
      margin-top: 40px;
    }
    & .lists_item {
      lost-column: 1/3;
      height: 200px;
      background: #FDFDFD;
      -webkit-box-shadow: 0 1px 20px rgba(0,0,0,.2);
              box-shadow: 0 1px 20px rgba(0,0,0,.2);
      border-radius: 4px;
      margin-bottom: 20px;
      position: relative;
      & a {
        position: absolute;
        left: 0;
        top: 0;
        bottom: 0;
        right: 0;
        display: block;
        text-decoration: none;
      }
      & h4 {
        font-weight: 700;
        margin-bottom: 10px;
        margin-top: 10px;
        margin-left: 10px;
        font-size: 16px;
        font-weight: 500;
        color: #100F2B;
      }
      & p {
        padding-top: 0;
        margin-top: 0;
        margin-bottom: 10px;
        font-weight: 500;
        font-size: 14px;
        font-weight: 300;
        color: #100F2B;
        margin-left: 10px;
        text-decoration: none !important;
      }
    }
  }


  .section {
    margin-top: 40px;
    lost-utility: clearfix;
    padding-bottom: 100px;
    & .section_wrapper {
        lost-column: 1/1;
    }
    & .wrapper_tagline {
        margin: 0;
        padding-bottom: 10px;
        border-bottom: 1px solid #D4D4D4;
        & .subtitle {
            margin: 0 !important; 
            padding: 0 !important; 
            color: #100F2B;
            font-weight: 600;
            font-size: 14px;
            position: relative;
            display: inline-block;
        }
        & h5:after {
            position: absolute;
            content: "";
            left: 0;
            bottom: -11px;
            height: 1px;
            background: #100F2B;
            width: 100%;
            display: block;
        }
    }
}

  



