textarea {
    outline: none;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    -webkit-appearance: none;
    background-color: #E2E4E6;
    border: 1px solid #CDD2D4;
    border-radius: 3px;
    display: block;
    margin-bottom: 9pt;
    min-height: 2pc;
    padding: 7px;
    resize: vertical;
    width: 100%;
    font-size: 14px;
    line-height: 40px;
    clear: both;
    font-weight: 400;
    margin: 0 0 4px;
    overflow: hidden;
    word-wrap: break-word;
    color: #4d4d4d;
    &.white {
        background: transparent;
        border: none;
    }
}


